header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:30px;
  background: #000;
  color:#fff;
    a {
      margin:0 10px;
      color:#fff;
      text-decoration: none;
    }
}
main{
    width: 90%;
    margin:30px auto 0;
    &.main {width: 100%; margin:0 auto;}
    input {
      width: 500px; height:30px;
      margin-left:50px;
      border:0; border-bottom: 1px solid #b7b7b7;
      &:focus{
        outline: none;
      }
    }
    > div {
      width: 100%;
      text-align: center;
      margin:30px 0;

      &.active {
        button:nth-of-type(1){
          display: none;
        }
        button:nth-of-type(2){
          display: inline-block;
        }
      }

      
      button:nth-of-type(2){
        display: none;
      }

    }
    
    button {
      border:0;
      width: 200px;
      background: #000;
      color:#fff;
      padding:10px;
    }
  section {
    display: flex;
    flex-wrap: wrap;
    &.de{
      width: 100%;
      height: 85vh;
      > figure {
        width: 100%;height: 100%;
        margin:0px auto;
        position: relative;
        > img {
          width: 100%;height: 100%;
          object-fit: cover;
        }
        > figcaption {
          position: absolute;
          top:0; left:0;
          width: 100%; height: 100%;
          background: rgba(0,0,0,.5);

          display: flex;
          flex-direction: column;
          align-items: center; justify-content: center;

          > p {
            font-size: 40pt;
            font-weight: 700;
            color:#fff;
            border-bottom:1px solid #fff;
            padding-bottom:10px; margin-bottom:10px; margin-top:0;
          }
          > span {
            color: #fff;
            font-size: 1rem;
            text-align: center;
            width: 50%; margin:0px auto;
          }
        }
      }
    }
    div{
      width: 25%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      a { text-decoration: none; color:#000; text-align: center;}
      &.swiper {width: 100%;
      }
      > div.swiper-wrapper {
        flex-direction: row;
        justify-content: left;
        .swiper-slide {
          a { 
            text-decoration: none;
            color:#000;
            figure{
              margin:0;
            }
          }

        }
      }
      figure {
        p {
          img {
            width: 100%;
            object-fit: cover;
          }
        }
      }
    }
    div.video{
      width: 100%;
      > div {
        width: 90%;
        display: block;
        > ul {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          list-style: none;
          gap:45px;
          justify-content: flex-start;
        }
      }
      section {
        width: 90%;
        margin:0px auto;
        display: block;
        >div{
          display: block;
          width: 100%;
          padding-bottom:40px;
          border-bottom:1px solid #b7b7b7;
          margin:40px auto;
          p {
            font-size: 18px;
          }
        }
      }
    } 
  }
}
